import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ViewController } from 'ionic-angular';
import { ImageViewerController } from 'ionic-img-viewer';
import { ScreenOrientation } from '@ionic-native/screen-orientation';


/**
 * Generated class for the PrincipalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-principal',
  templateUrl: 'principal.html',
})
export class PrincipalPage {

 testimonials = [
    {
      imageUrl: 'assets/img/2.jpg'
    },
    {
      imageUrl: 'assets/img/3.jpg'
    },
    {
      imageUrl: 'assets/img/18-a.jpg'
    },
    {
      imageUrl: 'assets/img/18-b.jpg'
    },
    {
      imageUrl: 'assets/img/19-a.jpg'
    },
    {
      imageUrl: 'assets/img/19-b.jpg'
    },
    {
      imageUrl: 'assets/img/20-a.jpg'
    },
    {
      imageUrl: 'assets/img/20-b.jpg'
    },
    {
      imageUrl: 'assets/img/21.jpg'
    },
  ];



  _imageViewerCtrl: ImageViewerController;

  public items: any = [];
  public error: string;
  public images: any = [];



constructor(public navCtrl: NavController, public navParams: NavParams,public viewCtrl: ViewController,imageViewerCtrl: ImageViewerController,public screenOrientation: ScreenOrientation) {
    this._imageViewerCtrl = imageViewerCtrl;
    //screenOrientation.lock(screenOrientation.ORIENTATIONS.PORTRAIT);
  }
  ionViewDidLoad() {
    this.viewCtrl.setBackButtonText('');
  }

  presentImage(myImage) {
    const imageViewer = this._imageViewerCtrl.create(myImage);
    imageViewer.present();
    this.viewCtrl.setBackButtonText('');
  }

   buscaridentificador(urlimagenes: string) {
    this.navCtrl.push('SliderWithArrowsPage', urlimagenes);
  } 


}



